<template>
  <div class="predictcom1">
    <el-form :label-position="labelPosition" :model="form" status-icon :rules="rules" ref="form" label-width="120px" class="demo-form">
      <iframe :src="src" id='myiframe' ref="iframe" width=700px height=750px
              frameborder=”0” border=”0″ marginwidth=”0″ marginheight=”0″ scrolling="no" prop="demo"
              v-if="hackReset == true"
      ></iframe>
      <!--smiles-->
      <el-form-item label="firstSmiles" prop="firstsmi">
        <el-input id="first" v-model="form.firstsmi" value="form.firstsmi" placeholder="Please enter the first smiles"></el-input>&nbsp;
        <el-button type="primary" id="but1" @click="getSmiles('first')">first  smiles</el-button>
      </el-form-item>
      <el-form-item label="secondSmiles" prop="secondsmi">
        <el-input id="second" v-model="form.secondsmi" value="form.secondsmi" placeholder="Please enter the second smiles"></el-input>&nbsp;
        <el-button type="primary" id="but2" @click="getSmiles('second')">second smiles</el-button>
      </el-form-item>
      <!--length range-->
        <el-form-item label="length range" prop="minlen">
          <el-tooltip placement="top">
            <div slot="content">Length of fragment</div>
            <span >min&nbsp;
              <el-input-number v-model="form.minlen" controls-position="right"  :min="0" :max="100"></el-input-number>
            </span>&nbsp;
          </el-tooltip>
        </el-form-item>
          <el-form-item  prop="maxlen">
          <span > max
          <el-input-number v-model="form.maxlen" controls-position="right"  :min="form.minlen+1" :max="101"></el-input-number>
        </span>
        </el-form-item>

      <!-- Whether to use own model-->
      <el-form-item label-width="200px" label="Use your own model" prop="ifOwnModel">
        <el-tooltip placement="top">
          <div slot="content">Whether to use your own model sampling</div>
          <el-switch
              v-model="form.ifOwnModel"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="Yes"
              inactive-text="No">
          </el-switch>
        </el-tooltip>
      </el-form-item>
      <!--choose model-->
      <el-form-item  label="Upload Model" v-if="form.ifOwnModel" prop="uploadmodel">
        <UploadCom :isSDF="false"
                   tipmsg="Upload your own  model for sampling" @func="getModel"
                   v-if="hackReset == true"
        ></UploadCom>
      </el-form-item>
      <!--Using the existing model-->
      <el-form-item v-show="!form.ifOwnModel" label="Other Models" prop="model_rule">
        <el-tooltip placement="top">
          <div slot="content">Select the model provided by the system</div>
          <el-select v-model="form.selected_model" >
            <el-option
                v-for="item in models"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-tooltip>
      </el-form-item>
<!--      &lt;!&ndash;n_best&ndash;&gt;-->
<!--      <el-form-item label="n_best" prop="n_best">-->
<!--        <el-tooltip placement="top">-->
<!--          <div slot="content">If verbose is set, will output the n_best decoded sentences-->
<!--            <br>-->
<!--            Default: 10</div>-->
<!--          <el-input-number v-model="form.besttop" controls-position="right"  :min="1" :max="1000"></el-input-number>-->
<!--        </el-tooltip>-->
<!--      </el-form-item>-->
      <!--beam size-->
      <el-form-item label="beam size" prop="beam_size">
        <el-tooltip placement="top">
          <div slot="content">Beam size
            Default: 10</div>
          <el-input-number v-model="form.beam_size" controls-position="right"  :min="10" :max="30"></el-input-number>
        </el-tooltip>
      </el-form-item>
      <!--n_best-->
      <el-form-item label="n_best" prop="n_best">
        <el-tooltip placement="top">
          <div slot="content">n_best
            Default: 10</div>
          <el-input-number v-model="form.n_best" controls-position="right"  :min="10" :max="300"></el-input-number>
        </el-tooltip>
      </el-form-item>
       <!--random_num-->
      <el-form-item label="random num" prop="random_num">
        <el-tooltip placement="top">
          <div slot="content">Size of the randomized fragments List
            Default: 1000</div>
          <el-input-number v-model="form.random_num" controls-position="right"  :min="1" :max="10000"></el-input-number>
        </el-tooltip>
      </el-form-item>
      <!--email-->
      <el-form-item label="Email" prop="email">
        <el-input v-model="form.email" autocomplete="True" placeholder="Please enter your email address"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">submit</el-button>
        <el-button @click="resetForm('form')">reset</el-button>
      </el-form-item>
    </el-form>
    <el-card shadow="always" v-show="ifrun">
      <h2>The child process is {{pid}}</h2>
    </el-card>
  </div>
</template>

<script>
import {getListen} from '@/api/Get'
import UploadCom from "@/components/UploadCom";
export default {
  name: "PredictCom1",
  components: {
    UploadCom
  },
  data() {
    return {
      src: './static/demo.html',
      iframeWin: {},
      labelPosition:'left',
      ifrun:false,//ifrun==true,show pid
      pid: '',
      models: [
        { label: 'ChEMBL', value: 'ChEMBL_model_average.pt' },
        { label: 'ZINC', value: 'zinc_3000w.pt' }
      ],
      hackReset: true,
      form:{
        firstsmi:'',
        secondsmi:'',
        minlen:7,
        maxlen:10,
        ifOwnModel:true,
        modelPath:'',
        selected_model: 'ChEMBL_model_average.pt',
        beam_size:10,
        n_best:10,
        random_num:1000,
        email:''
      },
      rules:{
        firstsmi:[
          {required: true, message: 'Please enter the smiles', trigger: 'blur' },
        ],
        secondsmi:[
          {required: true, message: 'Please enter the smiles', trigger: 'blur' },
        ],
        email:[
          {required: true, message: 'Please enter your email address', trigger: 'blur' },
          { type: 'email', message: 'Please enter the correct email address', trigger: ['blur', 'change'] }
        ],

      }
    }
  },
  props: {
    url: String,
  },
  methods: {
    getSmiles: function (id) {
      if (id === 'first') {
        this.form.firstsmi = this.$refs.iframe.contentWindow.new_smiles
        this.$refs.iframe.contentWindow.jsmeApplet.reset()
      } else if (id === 'second') {
        this.form.secondsmi = this.$refs.iframe.contentWindow.new_smiles
        this.$refs.iframe.contentWindow.jsmeApplet.reset()
      }
    },
    getModel(data){
      this.form.modelPath=data
      // console.log('model:   '+this.form.modelPath);
    },
    submitForm(formName) {
      var checkS = this.checkSubmit()
      this.$refs[formName].validate((valid) => {
        if (valid && checkS) {
          // 发送请求
          getListen(this.url,this.form).then(res=>{
            // console.log(res);
            this.pid = res.pid+3
            this.ifrun = true
            this.$notify({
              title: 'Training is running',
              message: 'The child process is '+this.pid,
              type: 'success'
            });
          }).catch(err=>{
            // console.log(err);
            this.$alert(err, 'Run failed', {
              confirmButtonText: 'confirm',})
          })
        }else{
          this.$alert('Input information is incomplete or incorrect','Submit failed', {
            confirmButtonText: 'confirm',
            type: 'warning'})
          return false;
        }
        // } else {
        //   // console.log('error submit!!');
        //   this.$message({
        //     showClose: true,
        //     message: 'Input information is incomplete or incorrect',
        //     type: 'error'
        //   });
        //   return false;
        // }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true;
      });
    },
    checkSubmit() {
      if (this.form.ifOwnModel==true && this.form.modelPath==''){
        return false
      }else {
        return true
      }
    },
    // runsampling:function () {
    //   getListen(this.url,this.form).then(res=>{
    //     // console.log(res);
    //     this.pid = res.pid
    //     // console.log(this.pid);
    //   }).catch(err=>{
    //     console.log(err);
    //   })
    // }
  },
  updated () {
    if(this.form.n_best<this.form.beam_size){
      this.form.beam_size=10
    }
  }
}

</script>

<style scoped>
.el-button{
  width:120px;
  height:40px;

}
.el-input{
  width:410px;
}
.el-card {
  width: 48%;
}
</style>
